.buttonContainer {

  .button {
    border-color: #20c997;
    color: #20c997;
    border-width: 2px;
    font-weight: 500;
    padding: 0.8rem 2.6rem;
    transition: all .5s ease;
    border-radius: 30px;
    background: transparent;
    box-shadow: none;

    &:hover {
      background: #20c997;
      color: white;
    }
  }

  .grayButton {

    border-color: #6c757d;
    color: #6c757d;
    border-width: 2px;
    font-weight: 500;
    padding: 0.8rem 2.6rem;
    transition: all .5s ease;
    border-radius: 30px;
    background: transparent;
    box-shadow: none;

    &:hover {
      background: #6c757d;
      color: white;
    }
  }
  .fullButton{
      border-color: #20c997;
      color: white;
      background: #20c997;
      border-width: 2px;
      font-weight: 500;
      padding: 0.8rem 2.6rem;
      transition: all .5s ease;
      border-radius: 30px;
      box-shadow: none;

      &:hover {
        background: rgba(32, 201, 151, 0.73);
        color: white;
      }
  }

}